.sectors{
    position: relative;
    .pulse{
        background-color: var(--navyD);
        position: absolute;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
        border: 1px solid var(--yellow);
        .before, .after {
            border: 1px solid var(--yellow);
            box-shadow: 0px 0px 2px var(--yellow);
            width: 100%;
            display: block;
            position: absolute;
            height: auto;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            border-radius: 100%;
            transform-origin: center center;
            opacity: 0;
            box-sizing: border-box;
            background-color: var(--navyD);
        }

        .before {
            animation: 1.5s .1s pulse infinite linear;
            animation-play-state: running;
        }

        .after {
            animation: 1.5s pulse2 infinite linear;
            animation-play-state: running;
        }
    }
    .tab-content{
        position: relative;
        .tab-pane{
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 60%;
            height: 60%;
            z-index: 100;
        }
    }
    .nav-pills{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 100;
        .nav-item{
            position: absolute;
            button{
                background: transparent;
                color: var(--white) !important;
                padding: 0 !important;
                transform-origin: center;
                .blast-btn{
                    position: absolute;
                    width: 20px;
                    height: 20px;
                    background: var(--yellow);
                    border-radius: 50%;
                    @extend.cubicTransition;
                    transform-origin: center;
                    transform: translateX(-50%) scale(1);
                }
                h4{
                    width: 200px;
                }
                &:hover, &.active{
                    opacity: 1;
                    .blast-btn{
                        transform: scale(2);
                        background: var(--success) !important;
                    }
                }
            }
            &:first-child(){
                top: 8%;
                left: 14%;
                button{
                    .blast-btn{
                        top: 0;
                        right: 0;
                    }
                }
            }
            &:nth-child(2){
                top: 14%;
                right: 21%;
                button{
                    .blast-btn{
                        top: 0;
                        left: 50%;
                    }
                }
            }
            &:nth-child(3), &:nth-child(4), &:nth-child(5){
                button{
                    .blast-btn{
                        top: 0;
                        left: 50%;
                    }
                }
            }
            &:nth-child(3){
                top: 62%;
                right: 15%;
            }
            &:nth-child(4){
                top: 96%;
                left: 28%;
            }
            &:nth-child(5){
                top: 50%;
                left: 14.5%;
            }
        }
    }
    .fade:not(.show) {
        visibility: hidden;
    }
}

