// Extra large devices (large desktops, 1200px and up)
@media (min-width: 1200px) and (max-width: 1440px){
}


// Large devices (desktops, 992px and up)
@media (min-width: 992px) and (max-width: 1199.98px){
    .sectors {
        .nav-pills {
            .nav-item{
                &:nth-child(2) {
                    right: 19%;
                }
                &:nth-child(3) {
                    right: 13%;
                }
                &:nth-child(4) {
                    top: 97%;
                }
                &:nth-child(5) {
                    left: 12.5%;
                }
            }
        }
    }
}


// Comon mobile & Tablet
@media (min-width: 100px) and (max-width: 1000px){
    .sectors {
        .nav-pills {
            height: 34vh;
            .nav-item {
                button {
                    h4{
                        width: 100px;
                    }
                }
            }
        }
        .pulse{
            position: relative;
            transform: none;
            left: initial;
            right: initial;
        }
        .tab-content{
            width: 100% !important;
            padding-top: 0 !important;
            margin-top: 100px;
            .tab-pane{
                position: relative;    
                top: 0;
                left: 0;
                transform: none;
                width: 100%;
                height: auto;
            
            }
        }
    }
}


// Medium devices (tablets, 768px and up)
@media (min-width: 768px) and (max-width: 819.98){
