            .nav-item{
                h4{
                    font-size: 1.2rem !important;
                }
                &:first-child(){
                    top: 6%;
                    left: 22%;
                }
                &:nth-child(2) {
                    top: 10%;
                    right: 27%;
                }
                &:nth-child(3) {
                    right: 22%;
                }
                &:nth-child(4) {
                    top: 75%;
                }
                &:nth-child(5) {
                    left: 19%;
                }
            }
        }
    }
}

