.scroll-x-wrapper{
    max-width: 100vw;
    overflow-x: scroll;
    position: relative;
    scrollbar-color: var(--dark);
    scrollbar-width: thin;
    -ms-overflow-style: none;
}
.scroll-x-wrapper::-webkit-scrollbar {
    height: 6px;
}

.scroll-x-wrapper::-webkit-scrollbar-thumb {
    height: 6px;
    background-color: var(--dark);
    opacity: 1;
    //background-image: url("../../images/static/icons/dragger.svg");
    -webkit-border-radius: 10px;
    border-radius: 10px;
}

.scroll-x-wrapper::-webkit-scrollbar-thumb:hover {
    opacity: .3;
}

.scroll-x-wrapper::-webkit-scrollbar:vertical {
    display: none;
}
@media (any-hover: none) {
    .pseduo-track {
        display: none;
    }
}
.pseduo-track {
    background-color: var(--dark);
    opacity: .3;
    height: 1px;
    width: 100%;
    position: relative;
    top: -3px;
    z-index: -10;
}


.scroll-y-wrapper{
    max-height: 100vh;
    overflow-y: scroll;
    position: relative;
    scrollbar-color: var(--dark);
    scrollbar-width: thin;
    -ms-overflow-style: none;
}
.scroll-y-wrapper::-webkit-scrollbar {
    width: 6px;
}

.scroll-y-wrapper::-webkit-scrollbar-thumb {
    width: 6px;
    background-color: var(--dark);
    opacity: 1;
    //background-image: url("../../images/static/icons/dragger.svg");
    -webkit-border-radius: 10px;
    border-radius: 10px;
}

.scroll-y-wrapper::-webkit-scrollbar-thumb:hover {
    opacity: .3;
}

.scroll-y-wrapper::-webkit-scrollbar:horizontal {
    display: none;
}
.pseduo-y-track {
    background-color: var(--dark);
    opacity: .3;
    width: 1px;
    height: 100%;    
    position: absolute;
    /* top: 0; */
    z-index: -10;
    left: calc(30% - 3px);
}
.ar{
    .pseduo-y-track {
        right: calc(30% - 3px);
        left: inherit;
    }
}

// Extra small devices (portrait phones, less than 576px)
@media (max-width: 655.98px){
