
.outline-letter{
    //top: 0;
    right: 0;
    line-height: 100%;
    font-size: 315px;
}
.ar{
    .outline-letter{
        left: 0;
        right: initial;
    }
}
.hero{
    //height: 110vh;
    h1{
        opacity: 0;
        &.loaded{
            opacity: 1;
        }
        span{
            &:first-child(){
                font-size: 2.2rem;
                transform: translateY(20px);
            }
            &:nth-child(2){
                letter-spacing: 2px;
            }
        }
    }
    .input-group{
        display: none;
        input{
            z-index: 100;
        }
    }

    .heroSwiper{
        margin-top: 10px;
        .swiper-slide{
            overflow: hidden;
            p{
                width: 100%;
            }
        }
    }
    .outline-letter{
        right: 16%;
        transform: scaleX(1.1);
        font-size: 315px !important;
        
    }
    &.first-fold{
        .col-lg-6{
            img.w-lg-70{
                max-height: 275px;
                object-fit: contain;
            }
        }
    }
}

.ar{
    .hero{
        //height: 110vh;
        h1{
            span{
                &:first-child {
                    transform: translateY(20px) translateX(50px);
                }
                &:nth-child(2){
                    letter-spacing: 0;
                    width: 50% !important;
                }
                &:nth-child(3){    
                    transform: translateX(40px);
                }
            }
        }
        .outline-letter {
            right: 0%;
            top: 10px;
            font-size: 250px!important;
        }
    }
}
.splitting .char {
    animation: slide-in 1s cubic-bezier(.5, 0, .5, 1) both;
    animation-delay: calc(100ms * var(--char-index));
}

@keyframes slide-in {
    from {
        transform: translateX(1em);
        opacity: 0;
    }
}
.hero-images{
    .hero-img-wrapper{
        &.active{
            opacity: 0;
            @extend.cubicTransition;
            transition-duration: 2s;
        }
    }
    &.loaded{
        .hero-img-wrapper{
            &.active{
                opacity: 1;
            }
        }
    }
}
.heroSwiper{
    .line{
        width: 40px;
        height: 1px;
        background-color: var(--yellow);
    }
    .swiper-pagination{
        bottom: 0;
        width: calc( 100% - 60px);
        margin-bottom: 0.5rem!important;
    }
    .swiper-button-next, .swiper-button-prev{
        width: 40px;
        height: 18px;
        margin-top: 0;
        color: var(--white);
        top: 0;
        left: initial !important;
        right: initial !important;
        &::after{
            display: none;
        }
    }
}

.first-fold{
    &.hero{
        //padding: 200px 0;
        padding: 140px 0 30px 0;
        height: 100vh;
    }
}
