.hero-img-wrapper{
    /* width: 54vw;
    height: 100%;
    position: absolute;
    bottom: -38%;
    right: -3vw; */
    position: absolute;
    width: 100%;
    height: 100%;
    @extend.cubicTransition;
    opacity: 0;
    visibility: hidden;
    z-index: 100;
    &.active, &.inside-page{
        visibility: visible;
        opacity: 1;
    }
    .el {
        width: 100%;
        height: 100%;
        background-size: contain;
        background-position: center;
        background-repeat: no-repeat;
        mask-image: url("../../assets/images/dynamic/home/Combined-Shape.svg");
        -webkit-mask-image: url("../../assets/images/dynamic/home/Combined-Shape.svg");
        mask-size: contain;
        -webkit-mask-size: contain;
        mask-repeat: no-repeat;
        -webkit-mask-repeat: no-repeat;
        mask-position: center;
        -webkit-mask-position: center;
    }
    .hero-image-anchor{
        display: none;
        position: absolute;
        bottom: calc(50% - 275px);
        left: calc(50% - 360px);
        width: 240px;
        height: 350px;
        z-index: 10;
        @extend.cubicTransition;
        opacity: 0;
        visibility: hidden;
        &:after{
            content: '';
            position: absolute;
            width: 240px;
            height: 350px;
            left: 5%;
            bottom: 5%;
            background: transparent;
            border: 1px solid var(--white);
            border-radius: .25rem!important;
        }
        a{
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: 11;
        }
        .with-line{
            position: relative;
            &:before{
                content: '';
                position: absolute;
                top: 50%;
                left: 0;
                width: 20px;
                height: 1px;
                background: var(--navyP);
                transform: translateY(-50%);
            }
        }
    }
    /* &:hover{
        .hero-image-anchor{
            visibility: visible;
            opacity: 1;
        }
    } */
    &.inside-page{
        width: 100%;
        right: 0;
        //height: 513px;
        height: 418px;
        //bottom: -312px;
        top: -120px;
        cursor: auto;
        .el{
            mask-image: url("../../assets/images/dynamic/home/Combined-Shape-Inside.svg") !important;
            -webkit-mask-image: url("../../assets/images/dynamic/home/Combined-Shape-Inside.svg") !important;
        }
    }
}
.ar{
    .hero-img-wrapper{
        right: initial;
        left: 1.5vw;
        .el{
            //transform: scaleX(-1);
            /* mask-image: url("../../assets/images/dynamic/home/Combined-Shape-ar.svg");
            -webkit-mask-image: url("../../assets/images/dynamic/home/Combined-Shape-ar.svg"); */
            mask-image: url("../../assets/images/dynamic/home/Combined-Shape.svg");
            -webkit-mask-image: url("../../assets/images/dynamic/home/Combined-Shape.svg");
        }
        .hero-image-anchor{
            right: calc(50% - 360px);
            left: inherit;
            .with-line{
                &:before{
                    right: 0;
                    left: inherit;
                }
            }
        }
    }
}
