// Comon mobile & Tablet
@media (min-width: 100px) and (max-width: 1000px){
    .first-fold.hero {
        height: auto;
        padding: 175px 0 30px;
    }
    .hero-images{
        width: 100%;
        position: relative;
        bottom: 0;
    }
    .hero-img-wrapper{
        width: 100%;
        /* width: calc(100% - 30px);
        right: 0; */
        &.inside-page {
            top: 30%;
            height: 350px;
            bottom: inherit;
        }
    }
    .ar{
        .hero-img-wrapper{
            left: 0;
        }
    }
}


// Medium devices (tablets, 768px and up)
@media (min-width: 768px) and (max-width: 991.98px){
    .outline-letter {
        right: 20%;
    }
    /* .hero-img-wrapper {
        height: 64%;
        bottom: -25%;
    } */

    .first-fold{
        padding: 220px 0 0;
    }
    .hero-img-wrapper.inside-page{
        position: relative;
        top: -15%;
    }
    .ar{
        .outline-letter {
            left: 20%;
        }
        .hero {
            .outline-letter {
                top: -90px;
            }
        }
    }
}


// Extra small devices (portrait phones, less than 576px)
@media (max-width: 655.98px){
    .hero-img-wrapper {
        &.inside-page{
            top: 0;
            height: 270px;
        }
        .hero-image-anchor{
            bottom: calc(50% - 92px);
            left: calc(50% - 130px);
            width: 115px;
            height: 160px;
            &:after{
                width: 115px;
                height: 160px;
            }
        }
    }
    .hero-images{
        height: 400px;
        overflow: hidden;
        position: relative;
    }
    .hero-img-wrapper {
        height: 100%;
        bottom: 0;
    }
    .hero {
        .outline-letter {
            font-size: 215px!important;
            right: 20%;
            top: 30%;
        }
        &.first-fold {
            height: auto;
        }
    }
    
    .card{
        .card-body{
            .card-content{
                margin-top: 10px;
            }
        }
    }
    .ar{
        .outline-letter {
            left: 18px;
        }
    }
}