
.nav-pills{
    .nav-link{
        background-color: var(--white);
        color: var(--navyP);
    }
}
.light-pills{
    &.nav-pills{
        .nav-link{
            background-color: transparent;
            color: var(--navyP) !important;
            &.active{
                background-color: var(--inactive);
                border: 1px solid var(--navyP);
            }
        }
    }
}
.tab-content{
    position: relative;
    .nav-pills{
        .nav-link{
            background: transparent;
            &.active{
                background: var(--navyP);
            }
        }
        &.light-pills{
            .nav-link{
                background-color: transparent;
                color: var(--navyP) !important;
                &.active{
                    background-color: var(--inactive);
                    border: 1px solid var(--navyP);
                }
            }
        }
    }
}
.bi-tab{
    &.active{
        @extend.bg-light-custom;
    }
}