/* -----------------------------------------------------
Header
----------------------------------------------------- */

.header{
  z-index: 1040;
  //transform: translateY(0);
  top: -140px;
  @extend.cubicTransition;
  //transition-delay: 1s;
  transition-duration: 1s;
  &:before{
    content: '';
    position: absolute;
    top: 37%;
    left: 0;
    width: 100%;
    height: 1px;
    opacity: 0.25;
    background: var(--white);
    transform: translateY(-50%);
  }
  &.bg-white-custom{
    //padding-top: 2rem!important;
    //padding-bottom: 2rem!important;
    //transform: translateY(-1rem);
    background-image: none !important;
    .text-white-custom{
      color: var(--navyP) !important;
    }
    .fill-white-custom{
      fill: var(--navyP) !important;
    }
    .header-search{
      .text-white-custom{
        color: var(--white) !important;
      }
    }
    &:before{
      background: var(--navyP) !important;
    }
  }
  &.fixed-top{
    animation: slideY 0.5s;
  }
  &.p-absolute{
    position: absolute;
    width: 100%;
  }
  &.loaded{
    top: 0;
  }
  .nav-item{
    position: relative;
  }
  .border-bottom {
    border-bottom: 1px solid rgba($color: #fff, $alpha: .25) !important;
  }
  .header-bottom{
    .align-self-end{
      //padding-bottom: 1.7rem;
      padding-bottom: 0.8rem;
    }
  }
}
.navbar-expand-lg {
  .navbar-collapse{
    display: block !important;
  }
}

.dropdown{
  &:hover {
    .dropdown-menu {
      display: block;
    }
  }
}
.header-bottom{
  .nav-item{
    a{
      text-decoration: none;
      &:hover{
        text-decoration: underline;
      }
    }
  }
}
/* .navbar .nav-item .dropdown-menu{ display: none; }
.navbar .nav-item:hover .dropdown-menu{ display: block; }
.navbar .nav-item .dropdown-menu{ margin-top:0; } */

.header-search{
  opacity: 0;
  visibility: hidden;
  top: 50px;
  //height: 90px;
  height: auto;
  width: 100%;
  @extend.cubicTransition;
  /* .bg-light-custom{
    @extend.cubicTransition;
    width: 0;
    right: 0;
  } */
  .input-group {
    height: 90px;
  }
  .close-btn{
    width: 45px;
    height: 100%;
    .leftright, .rightleft{
      background-color: var(--navyP);
      right: 10px;
      margin-top: calc(50% + 16px);
    }
  }
  &.open{
    opacity: 1;
    visibility: visible;
    /* .bg-light-custom{
      width: 100%;
    } */
  }
}
@media (min-width: 992px) {
  .ar{
    .header{
      direction: initial;
      .col-6.col-lg-9{
        direction: rtl;
      }
      .justify-content-end{
        justify-content: flex-start!important;
      }
      .text-lg-end{
        text-align: right!important;
      }
      .navbar-collapse{
        direction: rtl;
      }
    }
  }
}
// Large devices (desktops, 992px and up)
@media (min-width: 992px) and (max-width: 1199.98px){
      .sticky-lg-top {
        top: 111px;
        position: absolute;
        left: 0;
        width: 100%;
        z-index: 10;
        background-color: transparent;
        border-bottom: 1px solid hsla(0,0%,100%,.15);
        .d-lg-block {
          display: inline-block!important;
        }
    }
    .order-1 .nav-item {
        border-bottom: 1px solid hsla(0,0%,100%,.15);
    }
    .dropdown {
      .nav-link {
        .fa-chevron-down {
          display: none;
        }
      }
    }
    .navbar-nav {
      .nav-item{
        &.dropdown {
          .dropdown-menu {
            height: auto;
            position: relative;
            display: block;
            background: var(--navyP);
            margin-top: 0;
            padding-bottom: 1rem;
            .dropdown-item{
              padding: 0.5rem 2rem;
              color: var(--white)!important;
              font-size: .7125rem!important;
            }
          }
        }
      }
    }
    .order-lg-1 {
        order: 2!important;
    }
    .order-lg-2 {
        order: 1!important;
    }
    .header-bottom{
      padding-bottom: 1rem!important;
      padding-top: 1rem!important;
    }
    .navbar-collapse {
      position: fixed!important;
      width: 100vw;
      background: var(--navyP);
      overflow-x: hidden;
      overflow-y: scroll;
      top: 80px;
      left: 0;
      height: calc(100vh - 80px);
      ul{
        width: 100%;
        .nav-item{
          width: 100%;
        }
      }
      .flex-lg-row {
          display: block !important;
      }
      &.text-lg-end{
        text-align: start!important;
      }

  }
  .navbar-toggler{
    outline: 0;
    box-shadow: none !important;
    display: block !important;
    .navbar-toggler-icon{
      height: 3px;
      background: transparent;
      position: relative;
      border-radius: 2px;
      &:before, &:after{
        content: '';
        position: absolute;
        left: 0;
        width: 100%;
        height: 3px;
        background: var(--white);
        border-radius: 2px;
        top: 0;
      }
      &:before{
        transform: rotate(-45deg);
      }
      &::after{
        transform: rotate(45deg);
      }
    }
    &.collapsed{
      .navbar-toggler-icon{
        background: var(--white);
        &:before{
          top: -8px;
          transform: none;
        }
        &::after{
          top: 8px;
          transform: none;
        }
      }
    }
  }
  .order-2{
    .navbar-nav {
        .nav-link{
          font-size: .7125rem!important;
        }
    }
  }
  .navbar-nav {
      .nav-link{
        padding: 1rem!important;
      }
  }
  .header{
    &:before{
      display: none;
    }
    .header-bottom{
      .navbar-collapse{
        &.collapse {
          display: none !important;
          &.show{
            display: block !important;
          }
        }
      }
      .justify-content-end.flex-lg-row.d-inline-block.d-lg-none{
        display: block !important;
      }
      .justify-content-end.d-none.flex-lg-row.d-lg-inline-block{
        display: none !important;
      }
    }
    &:before {
      top: 25%;
    }
    .header-bottom {
      .align-self-end {
        //padding-bottom: 1.8rem;
        //padding-top: 1.8rem;
      }
    }
  }
  .multi-column{
    .col-lg-6, .col-6{
      flex: 0 0 100%;
      max-width: 100%;
    }
  }
  .ar{
    .header {
      &:before {
        top: 34%;
      }
      .header-bottom {
        .align-self-end {
          padding-bottom: 1rem;
        }
      }
    }
  }
}
// Comon mobile & Tablet
@media (min-width: 100px) and (max-width: 1000px){
  .header{
    &:before{
      display: none;
    }
    .fa-chevron-down{
      display: none;
    }
    .back-btn{
      display: none !important;
    }
  }
  .navbar-expand-lg {
    .navbar-collapse{
      display: none !important;
    }
  }
  .order-1{
    .nav-item{
      border-bottom: 1px solid rgba($color: #fff, $alpha: .15);
      .nav-link{
        justify-content: space-between!important;
        display: flex !important;
        i{
          //transform: rotate(-90deg);
          display: none;
        }
      }
    }
  }
  .navbar-nav {
    .dropdown-menu {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: calc(100vh - 78px);
      background: var(--navyP);
      margin-top: 0;
      padding: 0;
      .col-6{
        max-width: 100%;
        flex: 0 0 100%;
      }
      li{
        display: block;
        width: 100%;
        //border-bottom: 1px solid rgba($color: #fff, $alpha: .15);
        .dropdown-item{
          padding: 0.5rem 2rem;
          color: var(--white)!important;
          font-size: .7125rem!important;
        }
      }
    }
    .nav-item{
      &.dropdown{
        .dropdown-menu{
          height: auto;
          position: relative;
          display: block;
        }
      }
    }
  }
  .lang, .header-search-icon{
    padding: 1.2rem .5rem!important;
    font-size: 1rem !important;
  }
  .navbar-collapse{
    position: fixed !important;
    width: calc(100vw);
    background: var(--navyP);
    overflow-x: hidden;
    overflow-y: scroll;
    left: 0;
  }
  .navbar-toggler{
    outline: 0;
    box-shadow: none !important;
    .navbar-toggler-icon{
      height: 3px;
      background: transparent;
      position: relative;
      border-radius: 2px;
      &:before, &:after{
        content: '';
        position: absolute;
        left: 0;
        width: 100%;
        height: 3px;
        background: var(--white);
        border-radius: 2px;
        top: 0;
      }
      &:before{
        transform: rotate(-45deg);
      }
      &::after{
        transform: rotate(45deg);
      }
    }
    &.collapsed{
      .navbar-toggler-icon{
        background: var(--white);
        &:before{
          top: -8px;
          transform: none;
        }
        &::after{
          top: 8px;
          transform: none;
        }
      }
    }
  }
  .header-search{
    position: fixed !important;
    top: 0;
    width: 100%;
    height: 100vh;
    .input-group{
      border-bottom: 1px solid rgba($color: #000000, $alpha: .15);
    }
  }
}




// Medium devices (tablets, 768px and up)
@media (min-width: 656px) and (max-width: 991.98px){
  .header{
    .header-bottom{
      .align-self-end{
        padding-top:0.5rem;
        padding-bottom: 0.5rem;
      }
    }
  }
  .navbar-collapse{
    top: 111px;
    height: calc(100vh - 111px);
  }
}


// Extra small devices (portrait phones, less than 576px)
@media (max-width: 655.98px){
  .header-search-icon, .lang{
    padding: 1rem 0.3rem!important;
  }
  .navbar-toggler{
    font-size: 1rem;
    padding: 0.25rem 0.3rem;
  }
  .navbar-collapse{
    top: 80px;
    right: -15px;
    height: calc(100vh - 80px);
  }
  .header{
    .header-bottom{
      .align-self-end{
        //padding-top: 0.8rem;
        padding-bottom: 0.5rem;
        padding-top: 0.5rem;
      }
    }
  }
  .navbar-collapse{
    top: 90px;
    height: calc(100vh - 90px);
  }
}
