    .card-title{
        //color: var(--navyP) !important;
        font-size: 1.1rem !important;
        text-transform: capitalize
    }
    &.bg-navy-custom{
        .card-title, .text-navy-custom{
            color: var(--white) !important;
        }
    }
    .card-body{
        position: relative;
        .related-icon{
            left: inherit;    
            right: 0;
            &:after{
                left: inherit;  
                right: 15%;
            }
            p{
                @extend.fs-small;
            }
        }
        div.justify-content-between{
            position: absolute;
            bottom: 20px;
            width: calc(100% - 3rem);
            &.row, &.border{
                position: relative;
                bottom: 0;
            }
        }
    }
    &.scale-card{
        //cursor: pointer;
        @extend.cubicTransition;
        //transform: translate3d(0,100px,0) scale(1);
        transform: scale(1);
        &:hover{
            box-shadow: 0 .25rem .5rem rgba($black, .175)!important;
            //transform: translateZ(0) scale(1.03) !important;
            transform: scale(1.03) !important;
        }
    }
    &.hCard{
        .card-title{
            padding-bottom: 2px;
            background-image: linear-gradient(var(--yellow),var(--yellow));
            background-position: 0% 100%;
            background-repeat: no-repeat;
            background-size: 0% 3px;
            transition: background-size .3s;
            display: inline-block;
        }
        .img-wrapper{
            min-height: 500px;
            img{
                @extend.cubicTransition;
                transform: scale(1);
            }
        }
        .fa-arrow-circle-right{
            transform: translateX(0);
            @extend.cubicTransition;
        }
        &:hover{
            .card-title{
                background-size: 100% 3px;
            }
            .img-wrapper{
                img{
                    transform: scale(1.1);
                }
            }
            .fa-arrow-circle-right{
                transform: translateX(6px);
            }
        }
    }
    &.search-panel{
        bottom: -50px;
    }
}
.col-12{
    &.overflow-scroll{
        max-height: 600px;
    }
    .img-wrapper{
        &.between-size{
            img{
