.custom-icon{
    cursor: pointer;
    .custom-tooltip{
      //padding: 18px;
      text-align: left;
      position: absolute;
      top: calc(100% + 10px);
      left: calc(-158px + 15px);
      width: 300px;
      opacity: 0;
      visibility: hidden;
      z-index: 10;
      @extend.cubicTransition;
      &:after{
          content: '';
          position: absolute;
          top: -6px;
          left: 50%;
          transform: translateX(-50%);
          border-bottom: 6px solid var(--dark);
          border-left: 6px solid transparent;
          border-right: 6px solid transparent;
      }
      ul{
          li{
              display: block;
              img{
                  margin-right: 8px;
              }
          }
      }
      .new-notification{
        animation: none;
        top: 1.8rem;
        left: -12px;
        right: inherit;
  
      }
    }
    &:hover{
        .custom-tooltip{
            opacity: 1;
            visibility: visible;
        }
    }
}
