}
.close-btn{
width: 15px;
height: 24px;
position: relative;
display: block;
//top: calc(1rem + 45px);
//right: 52px;
.leftright, .rightleft{
    height: 2px;
    width: 15px;
    position: absolute;
    margin-top: 12px;
    background-color: var(--white);
    border-radius: 2px;
}
.leftright{
    transform: rotate(45deg);
    transition: all .3s ease-in;
}
.rightleft{
    transform: rotate(-45deg);
    transition: all .3s ease-in;
}
&:hover{
    .leftright, .rightleft{
        background-color: var(--primary);
    }
    .leftright{
        transform: rotate(-45deg);
    }
    .rightleft{
        transform: rotate(45deg);
    }
}
}
