.input-group{
    height: 56px;
    .form-label{
        top: 50%;
        left: 0;
        //width: 100%;
        height: auto;
        @extend.cubicTransition;
        cursor: text;
        //transform: translateY(-50%);
        transform-origin: 0 0;
    }
    .form-control{
        border: 0;
        //width: 100%;
        outline: 0;
        box-shadow: none;
        border-bottom: 1px solid rgba($color: #fff, $alpha: .25);
        height: 75% !important;
        margin-top: 14px;
    }
    .btn{
        top: 0;
        right: 0;
    }
    input:invalid {
      outline: 0;
    }
    input:focus,
    input:valid,
    input:invalid {
        border-bottom: 2px solid var(--white);
    }
    input:focus~label,
    input:valid~label,
    input:invalid~label {
      top: calc(50% - 14px);
      transform: translateY(-50%);
      font-size: .7rem!important;
    }
    &.light-bg{
        input:focus,
        input:valid,
        input:invalid {
            border-bottom: 2px solid var(--navyP);
        }
        textarea:focus{
            border-bottom: 2px solid var(--navyP);
        }
    }
    &.textarea{
        height: 300px;
        .form-label{
            top: 0;
        }
        textarea{
            padding-top: 30px !important;
            height: calc(100% - 15px)!important;
            border-bottom: none !important;
        }
        textarea:invalid {
            outline: 0;
        }
        textarea:focus,
        textarea:valid,
        textarea:invalid {
            border-bottom: 2px solid var(--white);
        }
        textarea:focus~label{
        top: calc(25px - 18px);
        font-size: .7rem!important;
        }
    }
}

.form-control.is-invalid:focus, .was-validated .form-control:invalid:focus{
