#calendar{
    a{
        @extend.font-r;
        @extend.text-navy-custom;
    }
    .fc-col-header, .fc-daygrid-body {
        width: 100% !important;
    }
    .fc-col-header{
        border: 0;
        border-top: 1px solid var(--navyP) ;
        border-bottom: 2px solid var(--navyP) ;
    }
    .fc-scrollgrid-sync-table{
        width: 100% !important;
        height: auto !important;
    }
    .fc-scroller-liquid-absolute, .fc-daygrid, .fc-bg-event, .fc-highlight, .fc-non-business, .fc-view-harness-active>.fc-view{
        position: relative !important;
    }
    .fc-view-harness{
        height: auto !important;
    }
    .fc-toolbar-title{
        @extend.font-m;
        @extend.text-navy-custom;
    }
    .fc-prev-button,.fc-next-button {
        @extend.bg-light-custom;
        border: none !important;
        .fa{
            @extend.text-navy-custom;
        }
    }
    .fc-daygrid-day-top{
        display: block;
    }
    .table-bordered {
        border: none;
        .fc-daygrid-day, .fc-day{
            padding: 1.25rem .75rem;
            text-align: center;
        }
        td, th{
            border: none;
        }
    }
    .fc-scrollgrid-sync-table{
        tr{
            border-bottom: 1px solid #dee2e6 ;
        }
    }
    .fc-daygrid-event, .fc-timegrid-event{
        background-color: var(--yellow);
        border: var(--yellow);
        .fc-event-main{
            @extend.text-navy-custom;
        }
    }
    .fc-daygrid-event-dot{
        border-color: var(--navyP);
    }
}