.cubicTransition{transition: all 500ms cubic-bezier(0.2, 0, 0.2, 1);-webkit-transition: all 500ms cubic-bezier(0.2, 0, 0.2, 1);-moz-transition: all 500ms cubic-bezier(0.2, 0, 0.2, 1);-o-transition: all 500ms cubic-bezier(0.2, 0, 0.2, 1);}

@keyframes bounce {
    from { transform: translate3d(0, -10px, 0);     }
    to   { transform: translate3d(0, 0, 0); }
}
@-webkit-keyframes bounce {
    from { -webkit-transform: translate3d(0, -10px, 0); transform: translate3d(0, -10px, 0); }
    to   { -webkit-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); }
}

@-moz-keyframes spin { 100% { -moz-transform: translate(0) rotate(360deg); } }
@-webkit-keyframes spin { 100% { -webkit-transform: translate(0) rotate(360deg); } }
@keyframes spin { 100% { -webkit-transform: translate(0) rotate(360deg); transform:translate(0) rotate(360deg); } }

@-moz-keyframes draw {  0% { width: 0; } 100% {width: 100%;} }
@-webkit-keyframes draw {  0% { width: 0; } 100% {width: 100%;} }
@keyframes draw {  0% { width: 0; } 100% {width: 100%;} }

@-moz-keyframes slide {  0% { transform: translateX(-100px); } 100% {transform: translateX(8px);} }
@-webkit-keyframes slide {  0% { transform: translateX(0); } 100% {transform: translateX(8px);} }
@keyframes slide {  0% { transform: translateX(0); } 100% {transform: translateX(8px);} }

@-moz-keyframes slideY {  0% { transform: translateY(-100px); } 100% {transform: translateY(0);} }
@-webkit-keyframes slideY {  0% { transform: translateY(-100px); } 100% {transform: translateY(0);} }
@keyframes slideY {  0% { transform: translateY(-100px); } 100% {transform: translateY(0);} }

@-webkit-keyframes spCircRot {
	from { -webkit-transform: rotate(0deg); }
	to { -webkit-transform: rotate(359deg); }
}
@keyframes spCircRot {
	from { transform: rotate(0deg); }
	to { transform: rotate(359deg); }
}


.scroll {
    position: absolute;
    display: block;
    font-size: 0.933rem;
    color: var(--primitive-color-black);
    text-decoration: none;
    padding: 0;
    bottom: 94px;
    left: 50%;
    transform: translateX(-50%);
    &::before {
        display: block;
        position: absolute;
        top:-2px;
        left:0;
        width: 28px;
        height: 43px;
        border: 2px solid var(--primitive-color-black);
        border-radius: 14px;
        content:"";
    }
    &::after {
        display: block;
        position: absolute;
        top:9px;
        left:13px;
        width: 2px;
        height: 8px;
        background: var(--primitive-color-black);
        border-radius: 1px;
        content:"";
        animation-name: scroll;
        animation-duration: 2s;
        animation-iteration-count: infinite;
        animation-delay: 2s;
    }
}  
@keyframes scroll {
    0% { opacity: 1; height: 8px;}
    20% { opacity: 1; height: 8px;}
    30% { transform: translateY(0); opacity: 1; height: 19px;}
    40% { opacity: 1;}
    80% { transform: translateY(19px); opacity: 0; height: 0;}
    81% { transform: translateY(0); opacity: 0; height: 8px;}
    100% { opacity: 1; height: 8px;}
} 


@keyframes pulse2 {
  0% {
    animation-timing-function: ease-in;
    transform: scale(0);
    opacity: 0;
  }
    40% {
      
    transform: scale(1.3);
    opacity: .2;
  }
  100% {
    animation-timing-function: ease-in-out;
    transform: scale(1.5);
    opacity: 0;
  }
}


@keyframes pulse {
  0% {
    animation-timing-function: ease-in;
    transform: scale(1.5);
  }
  40% {
    
    transform: scale(1.8);
    opacity: .2;
  }
  100% {
    animation-timing-function: ease-in-out;
    transform: scale(2);
    opacity: 0;
  }
}
.ar{
    @-moz-keyframes slide {  0% { transform: scaleX(-1) translateX(-100px); } 100% {transform: scaleX(-1) translateX(8px);} }
    @-webkit-keyframes slide {  0% { transform: scaleX(-1) translateX(0); } 100% {transform: scaleX(-1) translateX(8px);} }
    @keyframes slide {  0% { transform: scaleX(-1) translateX(0); } 100% {transform: scaleX(-1) translateX(8px);} }
}