
.notifications, .settings, .comment{
    position: fixed;
    top: 0;
    right: -35vw;
    //right: 0;
    width: 35vw;
    height: 100vh;
    z-index: 1071 !important;
    //padding: 32px;
    overflow-y: scroll ;
    @extend.cubicTransition;
    //background: var(--light);
    &.show{
        right:0;
    }
    section{
        padding: 16px 0;
    }
    .close-btn {
        width: 24px;
        height: 24px;
        position: relative;
        right: 24px;
        .leftright, .rightleft{
            width: 24px;
            background-color: var(--navyP);
        }
    }
    .border-bottom {
      border-bottom: 1px solid rgba($color: #E2E2E2, $alpha: 1) !important;
    }
    .goog-te-gadget{
        font-size: 0;
    }
    .goog-logo-link{
        display: none;
    }
}
.notification-anchor{
    &:hover{
        background-color: var(--inactive);
    }
}
.new-notification, .new-inner-notification{
    width: 8px;
    height: 8px;
    background-color: var(--red);
    border-radius: 50%;
}
.new-notification{
    position: absolute;
    top: 35%;
    right: 25%;
    animation: bounce 0.5s;
    animation-direction: alternate;
    animation-timing-function: cubic-bezier(.5,0.05,1,.5);
    animation-iteration-count: 5;
}

.ar{
    .notifications, .settings, .comment{
        left: -35vw;
        right: inherit;
        &.show{
            left:0;
        }
        .close-btn {
            left: 24px;
            right: inherit;
        }
    }
    .rsbtn {
        .rsbtn_play{
            float: right;
        }
    }
    .new-notification{
        left: 25%;
        right: inherit;
    }
}

// Comon mobile & Tablet
@media (min-width: 100px) and (max-width: 1000px){
    .notifications, .settings, .comment{
        right: -100vw;
        width: 100vw;
    }
    .ar{
        .notifications, .settings, .comment{
            left: -100vw;
        }
    }
}