.swiper {
    width: 100%;
    height: 100%;
}

.swiper-slide {

    /* Center slide text vertically */
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
    height: 1400px;
    overflow: hidden;
    .container{
        align-self: flex-start!important;
    }
}
.vSwiperLCardsDouble-1{
    .swiper{
        min-height: 600px;
    }
}

.swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.swiper-wrapper{
    &.mb-4{
        margin-bottom: 0.5rem!important;
    }
}
.vSwiper-wrapper{
    height: 1400px;
    overflow: hidden;
    .swiper-wrapper{
        //padding: 30px 0;
        //display: block;
        .swiper-slide{
            //display: block;
        }
    }
    &.gallery{
        height: 1000px;
        .img-wrapper{
            img, video{
                height: 90%;
            }
        }
    }
    &.timeline{
        height: 120vh !important;
        .swiper-slide {
            height: 90%;
        }
        .swiper-nav{
            top: 0;
            right: 20%;
            z-index: 10;
            height: 100%;
            width: 38px;
            left: inherit;
            .swiper-button-next{
                top: calc(90% - 44px);
            }
            .swiper-button-prev{
                top: 10%;
            }
        }
        .line{
            top: 0;
            right: 8%;
            width: 1px;
            height: 100%;
        }
        .bullets{
            top: 0;
            right: calc(8% - 15px);
            height: 100%;
            width: 80px;
            z-index: 1;
            .bullet-prev{
                top: 20%;
            }
            .bullet-next{
                top: 80%;
            }
            .bullet-active{
                top: 50%;
                cursor: auto;
                pointer-events: none;
                background-color: rgba($color: $primary , $alpha: .5);
            }
        }
    }
    &.msg{
        height: 420px;
    }
   /*  &:before{
        content: '';
        position: absolute;
        top: 0;
        left: 56px;
        width: 1px;
        height: 100%;
        background-color: var(--navyP);
        opacity: .3;
    } */
    .swiper-nav{
        //top: 50%;
        top: 0;
        left: 56px;
        z-index: 10;
        .nav-wrapper{
            transform: rotate(90deg) translateX(-30px);
            transform-origin: 0;
            text-align: center;
            width: 100%;
        }
        .swiper-pagination-bullet{
            width: 12px;
            height: 12px;
            background-color: var(--inactive);
            display: inline-block !important;
            margin: 0 3px !important;
            opacity: 1;
            border-radius: 50%;
            @extend.cubicTransition;
            &.swiper-pagination-bullet-active{
                width: 90px;
                background-color: var(--navyP);
                border-radius: 10px;
            }

        }
        .swiper-pagination{
            top: 0;
            transform: none;
            right: initial;
            left: initial;   
            margin-top: 10px; 
            margin-left: 1rem!important;
            margin-right: 1rem!important;  

        }
        .swiper-button-next, .swiper-button-prev{
            top: 0;
            margin-top: 0;
            &:after{
                display: none;
            }
        }
    }
    /* .swiper-slide{
        .card{
            transform: translate3d(0,100px,0);
            opacity: 0;
        }
    } */
    .swiper-slide{
        &.active, &.swiper-slide-active{
            .card{
                opacity: 1;
                transform: translateZ(0);
            }
        }
    }
}
/* .swiper-button-disabled {
    opacity: 0;
    visibility: hidden;
} */
.ar{
    .vSwiper-wrapper{
        .swiper-nav{
            right: 56px;
            left: inherit;
            .nav-wrapper{
                transform-origin: 100%;
                transform: rotate(-90deg) translateX(30px);
            }
        }
        &.timeline{
            .swiper-nav{
                left: 20%;
                right: inherit;
            }
            .line{
                left: 8%;
                right: inherit;
            }
            .bullets{
                left: calc(8% - 15px);
                right: inherit;
            }
        }
    }
}

@media (min-width: 1441px){
    .vSwiper-wrapper{
        &.timeline {
            height: 80vh !important;
        }
    }
}

// Large devices (desktops, 992px and up)
@media (min-width: 992px) and (max-width: 1199.98px){
    .vSwiper-wrapper {
        .swiper-nav {
            left: 25px;
        }
    }
    .ar{
        .vSwiper-wrapper {
            .swiper-nav {
                right: 25px;
                left: inherit;
            }
        }

    }
}

// Comon mobile & Tablet
@media (min-width: 100px) and (max-width: 1000px){
    .vSwiper-wrapper {
        &.timeline {
            height: 80vh!important;
            .swiper-slide {
                height: 94%;
            }
        }
        /* &:before{
            top: inherit;
            bottom: 27px;
            left: 0;
            width: 100%;
            height: 1px;
        } */
        .swiper-nav{
            bottom: 0;
            left: 50%;
            top: inherit;
            transform: translateX(-50%);
            width: 85%;
            .nav-wrapper {
                transform: none;
            }
            .swiper-pagination {
                margin-top: 9px;
            }
            .swiper-pagination-bullet{
                margin: 0 5px !important;
                width: 10px;
                height: 10px;
                &.swiper-pagination-bullet-active{
                    width: 100px;
                }
            }
        }
        &.vSwiperLCards {
            //height: 2840px;
        }
    }
    .row-reverse{
        .card{
            .row{
                flex-direction: row-reverse!important;
            }
        }
    }
}

// Medium devices (tablets, 768px and up)
@media (min-width: 768px) and (max-width: 991.98px){
    .vSwiper-wrapper{
        &.timeline {
            .swiper-nav {
                right: 188px;
            }
        }
    }
    .ar{
        .vSwiper-wrapper{
            &.timeline {
                .swiper-nav {
                    left: 188px;
                    right: inherit;
                }
            }
        }
    }
    .vSwiper-wrapper {
        //height: 2270px;
        &.gallery{
            //height: 1500px;
        }
    }
}

// Extra small devices (portrait phones, less than 576px)
@media (max-width: 655.98px){
    .vSwiper-wrapper{
        &.timeline {
            .swiper-nav {
                right: 88px;
            }
        }
    }
    .ar{
        .vSwiper-wrapper{
            &.timeline {
                .swiper-nav {
                    left: 88px;
                    right: inherit;
                }
            }
        }
    }
    .swiper-pagination{
        &.mx-4{
            margin-left: .7rem!important;
            margin-right: .7rem!important;
        }
    }
    .vSwiper-wrapper {
        //height: 1522px;
        &.gallery{
            //height: 2300px;
        }
    }
}