.footer{
    .border-bottom {
      border-bottom: 1px solid rgba($color: #fff, $alpha: .25) !important;
    }
    .input-wrapper{
        background: #334A81;
        input, .btn{
            outline: 0;
            box-shadow: none;
        }
        .form-control:-ms-input-placeholder{
            color: var(--inactive);
        }
        .form-control::-webkit-input-placeholder{
            color: var(--inactive);
        }
    }
}

.related-icon{
    cursor: pointer;
    position: relative;
    .related-tooltip{
        padding: 10px;
        text-align: left;
        position: absolute;
        bottom: calc(100% + 8px);
        //left: 50%;
        right: -25px;
        width: 220px;
        opacity: 0;
        visibility: hidden;
        z-index: 10;
        @extend.cubicTransition;
        //transform: translateX(-50%);
        //background-color: var(--white);
        &:after{
            content: '';
            position: absolute;
            bottom: -6px;
            //left: 50%;
            //transform: translateX(-50%);
            right: 10%;
            border-left: 6px solid transparent;
            border-right: 6px solid transparent;
        }
        &.bg-white-custom{
            &:after{
                border-top: 6px solid var(--white);
            }
        }
        &.bg-navy-custom{
            &:after{
                border-top: 6px solid var(--primary);
            }
        }
    }
    &:hover{
        .related-tooltip{
            opacity: 1;
            visibility: visible;
        }
    }
}
input.star {
    display: none
}
.card-footer{
    .related-icon{
        .related-tooltip{
            left: -8px;
            right: inherit;
            &:after{
                left: 10%;
                right: inherit;
            }
        }
    }
}

label.star {
    float: right;
    padding: 5px;
    @extend.cubicTransition;
    &:hover {
        transform: rotate(-15deg) scale(1.3)
    }
    .far{
        display: block;
    }
    .fas{
        display: none;
    }
}

input.star:checked~label.star{
    .far{
        display: none;
    }
    .fas{
        display: block;
    }
}
.sitemap-footer{
    a{
        text-decoration: none;
        &:hover{
            text-decoration: underline;
        }
    }
}
