// Large devices (desktops, 992px and up)
@media (min-width: 992px) and (max-width: 1199.98px){
    .footer-bottom{
        .d-flex.text-start{
            flex-wrap: wrap;
        }
    }
}

@media (min-width: 100px) and (max-width: 1000px){
    .footer-top{
        .col-6.col-md-4{
            //flex: 0 0 100%;
            //max-width: 100%;
            ul{
                text-align: end;
            }
        }
    }
    .footer-bottom{
        .d-flex.text-start{
            flex-wrap: wrap;
        }
    }
}
