html{	
    font-size: 16px;	
}
.fs-small{ font-size: 0.8125rem !important;}
.fs-custom{ font-size: 0.9rem !important;}


.text-dark-custom{
  color: var(--dark) !important;
}
.text-navy-custom{
  color: var(--navyP) !important;
}
.text-white-custom{
  color: var(--white) !important;
}
.text-navyS-custom{
  color: var(--navyS) !important;
}
.text-secondary-custom{
  color: var(--yellow) !important;
}
.text-dark {
  color: var(--dark)!important;
}
.bg-navy-custom{
  background-color: var(--navyP) !important;
  .fill-navy-custom{
    fill: var(--white) !important;
  }
  .text-navy-custom{
    color: var(--white) !important;
  }
  h3, p{
    color: var(--white) !important;
  }
}
.bg-navyD-custom{
  background-color: var(--navyD) !important;
}
.bg-white-custom{
  background-color: var(--white) !important;
}
.bg-dark-custom{
  background-color: var(--dark) !important;
}
.bg-light-custom{
  background-color: var(--light) !important;
}
.bg-gradient-custom{
  background-image: linear-gradient(45deg,var(--navyP) , var(--navyS) );
}
.bg-inactive-custom{
  background-color: var(--inactive) !important;
}
.bg-navyS-custom{
  background-color: var(--navyS) !important;
}
.bg-yellow-custom{
  background-color: var(--yellow) !important;
}
.stroke-white-custom{
  stroke: var(--white) !important;
}
.stroke-navy-custom{
  stroke: var(--navyP);
}
.fill-white-custom{
  fill: var(--white);
}
.fill-navy-custom{
  fill: var(--navyP);
}
.position-lg-absolute{
  position: absolute;
}
.bg-input{
  background-color: #F1F3F7 !important;
}
.font-outline{
  -webkit-text-stroke-width: .3px; 
  -webkit-text-stroke-color: var(--yellow); 
  -webkit-text-stroke: 2px var(--yellow);
  color: transparent;
}
.font-outline-white{
  -webkit-text-stroke-width: .3px; 
  -webkit-text-stroke-color: var(--white); 
  -webkit-text-stroke: 2px var(--white);
  line-height: 73% !important;
  font-family: sans-serif;
  color: transparent;
}
.font-outline-navy{
  -webkit-text-stroke-width: .3px; 
  -webkit-text-stroke-color: var(--navyP); 
  -webkit-text-stroke: 2px var(--navyP);
  line-height: 73% !important;
  font-family: sans-serif;
  color: transparent;
} 
.ar{
  .font-outline-white, .font-outline-navy{
    line-height: 84% !important;
  }
}
.square{
  padding-top: 100%;
  &.w-50{
    padding-top: 50%;
  }
}
.half-opacity{
  opacity: .5;
}
.display-2 {
  font-size: 4rem !important;
  line-height: 1.1 !important;
}
.display-4 {
  font-size: 3rem !important;
}
h4{
  font-size: 1.25rem !important;
}
h5{
  font-size: 1rem !important;
}
.aos-init{
  opacity: 1 !important;
}
$primary: #001E62;
$yellow: #DAAA00;
$success: #00C7B1;
$light: #F0F2F8;

$theme-colors: (
  "primary": $primary,
  "success": $success,
  "yellow": $yellow,
  "light": $light
);
$breadcrumb-divider: quote("__");


@import "../../../../node_modules/bootstrap/scss/bootstrap";

.w-lg-90, .w-90{width: 90%;}
.w-lg-80, .w-80{width: 80%;}
.w-lg-70, .w-70{width: 70%;}
.w-lg-60, .w-60{width: 60%;}
.w-lg-50, .w-50{width: 50%;}
.w-lg-40, .w-40{width: 40%;}
.w-lg-30, .w-30{width: 30%;}
.w-lg-20, .w-20{width: 20%;}
.w-lg-10, .w-10{width: 10%;}

.overflow-y-scroll{
  overflow-y: scroll;
}
.h-80v{
  max-height: 80vh;
}

.object-fit-cover{
  height: 100%;
  object-fit: cover;
}
.object-fit-contain{
  height: 100%;
  object-fit: contain;
}
.clear-both{
  clear: both;
}
.no-bg{
  background-color: transparent !important;
}
.d-list{
  display: list-item !important;
}
.disc-style{
  list-style: disc !important;
  padding: 0 15px;
}
.square-style{
  list-style: square !important;
}
.h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6, p{
  line-height: 1.5 !important;
}
.ar{
  direction: rtl;
  text-align: right;
  .me-2, .me-3, .me-4,.me-5 {
      margin-right: 0rem!important; 
  }
  [type=email], [type=number], [type=tel], [type=url] {
      direction: rtl;
  }
}


// Comon mobile & Tablet
@media (min-width: 100px) and (max-width: 1000px){
  .w-lg-90, .w-lg-80, .w-lg-70, .w-lg-60, .w-lg-50, .w-lg-40, .w-lg-30, .w-lg-20, .w-lg-10{width: 100%;}
  .display-1 {
      font-size: 4rem !important;
  }
  .display-2 {
      font-size: 3.5rem !important;
  }
  .display-3 {
      font-size: 2.5rem !important;
  }
  .h4, h4 {
      font-size: 1rem !important;
  }
  .parallax-container{
      transform: translateY(0) !important;
      margin-bottom: 0 !important;
  }
  .position-lg-absolute{
    position: relative;
  }
}
@media (min-width: 768px) and (max-width: 991.98px){
  .font-outline-white, .font-outline-navy{
    font-size: 220px !important;
  }
}
@media (max-width: 767.98px){
  .font-outline-white, .font-outline-navy{
    font-size: 110px !important;
  }
}